import { IAppStorage } from './AppStorageInterface';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { IEncrypt } from './Encryption/IEncrypt';

export class DeviceStorage implements IAppStorage {
    constructor(private readonly encrypt: IEncrypt) {}

    private getKey(key: string): string {
        return this.encrypt.hash(key);
    }

    async set(key: string, item: string): Promise<void> {
        await AsyncStorage.setItem(
            this.getKey(key),
            this.encrypt.encrypt(item)
        );
    }
    async get(key: string): Promise<any> {
        const value = await AsyncStorage.getItem(this.getKey(key));
        if (!value) {
            return null;
        }

        const decrypted = this.encrypt.decrypt(value);
        return decrypted;
    }

    async delete(key: string): Promise<void> {
        await AsyncStorage.removeItem(this.getKey(key));
    }
}
