export class Achievement {
    private _description: string;
    private _unlockTs: number;
    private _hidden: boolean;
    private _payload: any;

    constructor(public readonly id: string, public readonly title: string) {
        this._unlockTs = -1;
        this._hidden = false;
    }

    public get description(): string {
        return this._description;
    }

    public get unlockTs(): number {
        return this._unlockTs;
    }

    public get hidden(): boolean {
        return this._hidden;
    }

    public get payload(): any {
        return this._payload;
    }

    public isLocked() {
        return this._unlockTs < 0;
    }

    public withDescription(description: string): Achievement {
        this._description = description;
        return this;
    }

    public unlockedAt(ts: number): Achievement {
        this._unlockTs = ts;
        return this;
    }

    public setHidden(hidden: boolean): Achievement {
        this._hidden = hidden;
        return this;
    }

    public withPayload(payload: any) {
        this._payload = payload;
    }
}
