import { Audio } from 'expo-av';
import { Sound } from 'expo-av/build/Audio';

const BUTTON_PRESS = require('../../assets/sounds/button-press.mp3');
const BUTTON_PENALTY = require('../../assets/sounds/button-penalty.mp3');

export class Sounds {
    public static SOUND_BUTTON_PRESS = 'SOUND_BUTTON_PRESS';
    public static SOUND_BUTTON_PENALTY = 'SOUND_BUTTON_PENALTY';

    private _buttonPress: Sound | null = null;
    private _buttonPenalty: Sound | null = null;

    constructor(private muted: boolean = false) {}

    public mute() {
        this.muted = true;
    }

    public unmute() {
        this.muted = false;
    }

    public get buttonPress(): Sound | null {
        if (this.muted) {
            return null;
        }
        return this._buttonPress;
    }

    public get buttonPenalty(): Sound | null {
        if (this.muted) {
            return null;
        }
        return this._buttonPenalty;
    }

    public async init() {
        const press = await Audio.Sound.createAsync(BUTTON_PRESS);
        const penalty = await Audio.Sound.createAsync(BUTTON_PENALTY);

        this._buttonPress = press.sound;

        this._buttonPenalty = penalty.sound;
        this._buttonPenalty.setVolumeAsync(0.4);
    }

    public play(sound: string) {
        if (this.muted) {
            return;
        }

        switch (sound) {
            case Sounds.SOUND_BUTTON_PRESS:
                this._buttonPress?.replayAsync();
                return;
            case Sounds.SOUND_BUTTON_PENALTY:
                this._buttonPenalty?.replayAsync();
                return;
        }
    }
}
