import { Sound } from 'expo-av/build/Audio';
import { FC } from 'react';
import { BasicButtonComponent } from '../BasicButton';

interface IProps {
    text: string;
    onPress: CallableFunction;
    shadowHeight?: number;
    disabled?: boolean;
    sound?: string;
}

export const RedButton: FC<IProps> = ({
    text,
    onPress,
    shadowHeight,
    disabled,
    sound,
}) => {
    return (
        <BasicButtonComponent
            text={text}
            textColor="#fff"
            shadowColor="#DE0261"
            backgroundColor="#FF006E"
            shadowHeight={shadowHeight}
            onPress={onPress}
            disabled={disabled}
            sound={sound}
        />
    );
};
