import { Sound } from 'expo-av/build/Audio';
import { FC, useRef } from 'react';
import { Animated, Pressable, Text, View } from 'react-native';
import { Game } from '../services/Game';

interface IProps {
    text: string;
    backgroundColor: string;
    shadowColor: string;
    textColor: string;
    onPress: CallableFunction;
    shadowHeight?: number;
    disabled?: boolean;
    sound?: string;
}
/**
 * Press animation happens from growing outer container margin top at the same time as reducing shadow container's
 * padding bottom
 */
export const BasicButtonComponent: FC<IProps> = ({
    text,
    backgroundColor,
    shadowColor,
    textColor,
    onPress,
    shadowHeight,
    disabled,
    sound,
}) => {
    const SHADOW_HEIGHT = shadowHeight ?? 5;
    const PRESS_DURATION = 75;
    const RELEASE_DURATION = 120;
    const marginHeight = useRef(new Animated.Value(0)).current;
    const paddingHeight = useRef(new Animated.Value(SHADOW_HEIGHT)).current;

    const containerPaddingLeft = useRef(new Animated.Value(0)).current;
    const containerPaddingRight = useRef(new Animated.Value(0)).current;

    // UIs
    const borderRadius = 15;
    const padding = {
        paddingRight: 70,
        paddingLeft: 70,
        paddingTop: 20,
        paddingBottom: 20,
    };

    const denyPressPadding = 20;
    const denyPressAnimationTickMs = 30;

    return (
        // outer container
        <Animated.View
            style={{
                flexDirection: 'row',
                marginTop: marginHeight,
                paddingRight: containerPaddingRight,
                paddingLeft: containerPaddingLeft,
            }}
        >
            <Pressable
                onPress={() => {
                    if (sound) {
                        Game.sounds.play(sound);
                    }

                    if (disabled) {
                        Animated.sequence([
                            Animated.timing(containerPaddingLeft, {
                                useNativeDriver: false,
                                toValue: denyPressPadding,
                                duration: denyPressAnimationTickMs,
                            }),
                            Animated.timing(containerPaddingLeft, {
                                useNativeDriver: false,
                                toValue: 0,
                                duration: denyPressAnimationTickMs,
                            }),
                            Animated.timing(containerPaddingRight, {
                                useNativeDriver: false,
                                toValue: denyPressPadding,
                                duration: denyPressAnimationTickMs,
                            }),
                            Animated.timing(containerPaddingRight, {
                                useNativeDriver: false,
                                toValue: 0,
                                duration: denyPressAnimationTickMs,
                            }),
                            Animated.timing(containerPaddingLeft, {
                                useNativeDriver: false,
                                toValue: denyPressPadding,
                                duration: denyPressAnimationTickMs,
                            }),
                            Animated.timing(containerPaddingLeft, {
                                useNativeDriver: false,
                                toValue: 0,
                                duration: denyPressAnimationTickMs,
                            }),
                        ]).start(() => {
                            onPress();
                        });
                        return;
                    }
                    Animated.parallel([
                        Animated.sequence([
                            Animated.timing(paddingHeight, {
                                useNativeDriver: false,
                                toValue: 0,
                                duration: PRESS_DURATION,
                            }),
                            Animated.timing(paddingHeight, {
                                useNativeDriver: false,
                                toValue: SHADOW_HEIGHT,
                                duration: RELEASE_DURATION,
                            }),
                        ]),
                        Animated.sequence([
                            Animated.timing(marginHeight, {
                                useNativeDriver: false,
                                toValue: SHADOW_HEIGHT,
                                duration: PRESS_DURATION,
                            }),
                            Animated.timing(marginHeight, {
                                useNativeDriver: false,
                                toValue: 0,
                                duration: RELEASE_DURATION,
                            }),
                        ]),
                    ]).start(() => {
                        onPress();
                    });
                }}
            >
                {/* Button shadow container of shadowColor */}
                <Animated.View
                    style={{
                        backgroundColor: shadowColor,
                        borderRadius: borderRadius,
                        paddingBottom: paddingHeight,
                    }}
                >
                    {/* Button container of backgroundColor*/}
                    <View
                        style={{
                            backgroundColor,
                            borderRadius: borderRadius,
                            justifyContent: 'center',
                            flexDirection: 'row',
                            ...padding,
                        }}
                    >
                        <Text
                            style={{
                                color: textColor,
                                fontSize: 24,
                                fontFamily: 'Primary',
                            }}
                            selectable={false}
                        >
                            {text}
                        </Text>
                    </View>
                </Animated.View>
            </Pressable>
        </Animated.View>
    );
};
