import { IAppClock } from '../../../../utils/clock/AppClockInterface';
import {
    GameEvent,
    SimpleEventsBus,
} from '../../../../utils/events/SimpleEventsBus';
import { STAT_EVENTS } from '../../../events';
import { EventsFactory } from '../../../EventsFactory';
import { ITracker } from './ITracker';

export class ButtonPressTracker implements ITracker {
    constructor(private readonly _events: SimpleEventsBus) {
        this._events.subscribe(STAT_EVENTS.TOTAL_TAPS, (event: GameEvent) => {
            this.handleTotalTapsIncrease(event);
        });
    }

    toJSON(): string {
        return '';
    }

    fromJSON(json: string): void {}

    private handleTotalTapsIncrease(event: GameEvent): void {
        const clicksTriggers = [1, 5, 20, 100, 500];
        for (const trigger of clicksTriggers) {
            if (event.payload.value === trigger) {
                this._events.emit(
                    EventsFactory.achievementsTrackerTriggered({
                        id: `PRESS:${trigger}`,
                        payload: trigger,
                    })
                );
            }
        }
    }
}
