import { FC } from 'react';
import { ScrollView, View } from 'react-native';
import { Game } from '../services/Game';
import { AchievementItem } from './Achievements/AchievementItem';
import { AchievementsHeader } from './Achievements/Header';

interface IProps {
    onClose: CallableFunction;
}

export const AchievementsScene: FC<IProps> = ({ onClose }) => {
    const achievements = Game.achievements.getAll();
    return (
        <View
            style={{
                position: 'absolute',
                top: 0,
                bottom: 0,
                left: 0,
                right: 0,
                backgroundColor: '#fff',
                padding: 20,
            }}
        >
            <AchievementsHeader achievements={achievements} onClose={onClose} />
            <ScrollView
                style={{ marginTop: 70 }}
                showsVerticalScrollIndicator={false}
            >
                {achievements.map((achievement) => (
                    <AchievementItem
                        achievement={achievement}
                        key={achievement.id}
                    />
                ))}
            </ScrollView>
        </View>
    );
};
