import { GameEvent } from '../../utils/events/SimpleEventsBus';
import { IAppStorage } from '../../utils/storage/AppStorageInterface';

interface HistoryButtonItem {
    taps: number;
    expiredAt: number;
}
export class History {
    private STORAGE_KEY = 'HISTORY';
    constructor(private readonly storage: IAppStorage) {}

    private _buttons: HistoryButtonItem[] = [];

    public buttonExpired(event: GameEvent) {
        if (event.payload.taps) {
            this._buttons.push({
                taps: event.payload.taps,
                expiredAt: event.ts,
            });
        }
        this.persist();
    }

    public get buttons(): HistoryButtonItem[] {
        return this._buttons;
    }

    public persist() {
        this.storage.set(this.STORAGE_KEY, JSON.stringify(this._buttons));
    }

    public async init() {
        const json = await this.storage.get(this.STORAGE_KEY);
        if (json) {
            try {
                this._buttons = JSON.parse(json);
            } catch (e) {
                console.log('Unable to init History');
            }
        }
    }
}
