import { Achievement } from './Achievement';

export class AchievementsCollection {
    private _list: Achievement[];
    constructor() {
        this._list = [];
    }

    public get list() {
        return this._list;
    }

    public fromJSON(json: string): AchievementsCollection {
        this._list = [];
        const data: any = JSON.parse(json);
        for (const item of data) {
            this._list.push(
                new Achievement(item.id, item.title)
                    .withDescription(item.description)
                    .setHidden(!!item.hidden)
            );
        }
        return this;
    }

    public toJSON(): string {
        return JSON.stringify(
            this._list.map((item) => ({
                id: item.id,
                title: item.title,
                description: item.description,
                hidden: item.hidden,
            }))
        );
    }

    unlock(id: string, unlockTs: number, payload?: any): Achievement | null {
        let result = null;
        this._list = this._list.map((item) => {
            if (item.id !== id) {
                return item;
            }
            if (item.unlockTs > -1) {
                return item;
            }

            item.unlockedAt(unlockTs);
            if (payload) {
                item.withPayload(payload);
            }
            result = item;
            return item;
        });
        return result;
    }

    unlockAll() {
        for (const achievement of this._list) {
            this.unlock(achievement.id, 1);
        }
    }
}
