import { IAppStorage } from '../../utils/storage/AppStorageInterface';

export class UserSettings {
    private STORAGE_KEY = 'SETTINGS';
    constructor(private readonly storage: IAppStorage) {}

    private _muted: boolean = true;

    public setMuted(muted: boolean) {
        this._muted = muted;
        this.persist();
    }

    public get muted(): boolean {
        return this._muted;
    }

    public persist() {
        this.storage.set(
            this.STORAGE_KEY,
            JSON.stringify({
                muted: this._muted,
            })
        );
    }

    public async init() {
        const json = await this.storage.get(this.STORAGE_KEY);
        if (json) {
            try {
                this._muted = JSON.parse(json)?.muted;
            } catch (e) {
                console.log('Unable to init Settings');
            }
        }
    }
}
