export class TimingManager {
    constructor() {}

    public getCoolDown(counter: number): number {
        // return 2 * 1000;
        const coolDownsInSeconds = [
            5,
            10,
            20,
            30,
            60,
            90,
            120,
            180,
            360,
            720,
            1440,
            2880, // 0.5 hrs
            3600, // 1 hrs
            7200, // 2 hrs
            14400, // 4 hrs
            28800, // 8 hrs
            43200, // 12 hrs
        ];
        const index = counter > 0 ? counter - 1 : 0;
        if (index < coolDownsInSeconds.length) {
            return coolDownsInSeconds[index] * 1000;
        }
        return 64800 * 1000; // 18 hrs
    }

    public getPressTimeFrame(): number {
        // return 5 * 1000;
        return 24 * 60 * 60 * 1000;
    }

    public getPenaltyTime(counter: number): number {
        const penalty = Math.floor(this.getCoolDown(counter) * 0.01);
        if (penalty < 20000) {
            return 20 * 1000;
        }
        return penalty;
    }
}
