import { FC } from 'react';
import { Text, View } from 'react-native';
import { ClickSvg } from '../../Icons/AchievementIcon/ClickSvg';
import { LockedSvg } from '../../Icons/AchievementIcon/LockedSvg';
import { Achievement } from '../../services/Game/Achievements/Achievement';
import { AchievementIcon } from './AchievementItem/AchievementIcon';
import { AchievementTitle } from './AchievementItem/AchievementTitle';

interface IProps {
    achievement: Achievement;
}

const colorsConfig = {
    locked: {
        badgeBg: '#rgba(252, 246, 189, 0.4)',
        borderColor: '#ffd773',
        titleColor: '#ebb429',
        descriptionColor: '#FFC736',
    },
    unlocked: {
        badgeBg: '#FCF6BD',
        borderColor: '#FFC736',
        titleColor: '#4a4e69',
        descriptionColor: '#4a4e69',
    },
};

export const AchievementItem: FC<IProps> = ({ achievement }) => {
    const isLocked = achievement.unlockTs < 0;
    const colors = isLocked ? colorsConfig.locked : colorsConfig.unlocked;
    return (
        <View
            style={{
                borderColor: colors.borderColor,
                borderWidth: 1,
                borderRadius: 10,
                backgroundColor: colors.badgeBg,
                flexDirection: 'row',
                padding: 10,
                marginBottom: 10,
                alignItems: 'flex-start',
                opacity: isLocked ? 0.7 : 1,
            }}
        >
            <View
                style={{
                    backgroundColor: '#fff',
                    borderColor: colors.borderColor,
                    borderWidth: 1,
                    borderRadius: 50,
                    padding: 10,
                    opacity: isLocked ? 0.4 : 1,
                }}
            >
                <AchievementIcon achievement={achievement} />
            </View>

            <View
                style={{
                    marginLeft: 10,
                    flex: 1,
                }}
            >
                <View style={{ paddingTop: achievement.isLocked() ? 10 : 0 }}>
                    <AchievementTitle achievement={achievement} />
                </View>
                {!isLocked && (
                    <Text
                        style={{
                            color: colors.descriptionColor,
                            fontFamily: 'PrimaryLight',
                            fontSize: 14,
                        }}
                    >
                        {achievement.description}
                    </Text>
                )}
            </View>
        </View>
    );
};
