import { Settings } from 'react-native';
import { IAppClock } from '../utils/clock/AppClockInterface';
import { GameEvent, SimpleEventsBus } from '../utils/events/SimpleEventsBus';
import { IAppStorage } from '../utils/storage/AppStorageInterface';
import { ACHIEVEMENT_EVENTS, SETTINGS_EVENTS, USER_EVENTS } from './events';
import { Achievements } from './Game/Achievements';
import { ButtonController } from './Game/ButtonController';
import { History } from './Game/History';
import { UserSettings } from './Game/UserSettings';
import { UserStats } from './Game/UserStats';
import { Sounds } from './Sounds';

export class Game {
    private static _debug: boolean = false;
    private static _controller: ButtonController;
    private static _achievements: Achievements;
    private static _clock: IAppClock;
    private static _storage: IAppStorage;
    private static _userStats: UserStats;
    private static _events: SimpleEventsBus;
    private static _sounds: Sounds;
    private static _history: History;
    private static _settings: UserSettings;

    public static get clock(): IAppClock {
        return Game._clock;
    }

    public static get storage(): IAppStorage {
        return Game._storage;
    }

    public static get controller(): ButtonController {
        return Game._controller;
    }

    public static get achievements(): Achievements {
        return Game._achievements;
    }

    public static get stats(): UserStats {
        return Game._userStats;
    }

    public static get events(): SimpleEventsBus {
        return Game._events;
    }

    public static get sounds(): Sounds {
        return Game._sounds;
    }

    public static get history(): History {
        return Game._history;
    }

    public static get settings(): UserSettings {
        return Game._settings;
    }

    public static get debug(): boolean {
        return Game._debug;
    }

    public static enableDebug() {
        Game._debug = true;
    }

    public static async init(storage: IAppStorage, clock: IAppClock) {
        Game._clock = clock;
        Game._storage = storage;

        Game._events = new SimpleEventsBus();

        Game._userStats = new UserStats(clock, storage, this._events);
        await Game._userStats.init();

        Game._controller = new ButtonController(clock, storage);
        await Game.controller.init(Game._userStats.currentTaps);

        Game._achievements = new Achievements(clock, storage, this._events);

        Game._history = new History(storage);
        await Game._history.init();

        Game._settings = new UserSettings(storage);
        await Game._settings.init();

        Game.wireUpEvents();

        Game._sounds = new Sounds();
        await Game.sounds.init();
    }

    private static wireUpEvents() {
        Game._events.subscribe(USER_EVENTS.BUTTON_TAP, (event: GameEvent) => {
            Game._userStats.tap(event);
        });

        Game._events.subscribe(
            USER_EVENTS.BUTTON_TAP_PENALTY,
            (event: GameEvent) => {
                Game._userStats.tapPenalty(event);
            }
        );

        Game._events.subscribe(
            ACHIEVEMENT_EVENTS.TRACKER_TRIGGERED,
            (event: GameEvent) => {
                Game._achievements.triggered(event);
            }
        );

        Game._events.subscribe(
            USER_EVENTS.BUTTON_EXPIRED,
            (event: GameEvent) => {
                Game._userStats.expireButton();
                Game._history.buttonExpired(event);
            }
        );

        Game._events.subscribe(
            SETTINGS_EVENTS.TOGGLE_MUTE,
            (event: GameEvent) => {
                Game.settings.setMuted(!!event.payload?.muted);
            }
        );
    }
}
