import { IEncrypt } from './IEncrypt';
import CryptoJS from 'crypto-js';

export class DefaultEncrypt implements IEncrypt {
    private secret: string = '';

    constructor() {
        const a =
            '2LrK9fHw3je3GNNTmyh3TJarwi4BmsgmaL3ji51w30sah37YGXTFqqy7HPYyJQWVzzJ3Zw';
        this.secret =
            a.substring(10, 20) +
            a.substring(0, 10) +
            a.substring(30, 40) +
            a.substring(60, 70) +
            a.substring(50, 60);
    }

    encrypt(data: string): string {
        return CryptoJS.AES.encrypt(data, this.secret).toString();
        // return data;
    }
    decrypt(data: string): string {
        return CryptoJS.AES.decrypt(data, this.secret).toString(
            CryptoJS.enc.Utf8
        );
    }
    hash(data: string): string {
        return CryptoJS.MD5(`${data}:${this.secret}`).toString();
    }
}
