import { FC } from 'react';
import { Text } from 'react-native';
import { BlockSvg } from '../../../Icons/AchievementIcon/BlockSvg';
import { ClickSvg } from '../../../Icons/AchievementIcon/ClickSvg';
import { LockedSvg } from '../../../Icons/AchievementIcon/LockedSvg';
import { Achievement } from '../../../services/Game/Achievements/Achievement';

interface IProps {
    achievement: Achievement;
}

export const AchievementTitle: FC<IProps> = ({ achievement }) => {
    let title = achievement.title;
    if (achievement.hidden && achievement.isLocked()) {
        title = '???';
    }
    return (
        <Text
            style={{
                color: achievement.isLocked() ? '#ebb429' : '#4a4e69',
                fontFamily: 'Primary',
                fontSize: 16,
            }}
        >
            {title}
        </Text>
    );
};
