import { FC, useState } from 'react';
import { Text } from 'react-native';
import { useInterval } from '../../hooks/useInterval';

interface IProps {
    endTime: number;
    color: string;
}

const padTo2Digits = (value: number) => {
    return value.toString().padStart(2, '0');
};

const msToTimer = (milliseconds: number): string => {
    let seconds = Math.floor((milliseconds + 1000) / 1000);
    let minutes = Math.floor(seconds / 60);
    let hours = Math.floor(minutes / 60);

    seconds = seconds % 60;
    minutes = minutes % 60;
    hours = hours % 24;

    return `${padTo2Digits(hours)}:${padTo2Digits(minutes)}:${padTo2Digits(
        seconds
    )}`;
};

export const CountDown: FC<IProps> = ({ endTime, color }) => {
    const [remainingTime, setRemainingTime] = useState(endTime - Date.now());

    useInterval(() => {
        setRemainingTime(endTime - Date.now());
    }, 500);

    return (
        <>
            {remainingTime <= 0 && (
                <Text style={{ textAlign: 'center', fontSize: 20 }}> </Text>
            )}
            {remainingTime > 0 && (
                <Text
                    style={{
                        textAlign: 'center',
                        fontSize: 20,
                        color,
                        fontFamily: 'Primary',
                    }}
                    selectable={false}
                >
                    {msToTimer(remainingTime)}
                </Text>
            )}
        </>
    );
};
