import { useFonts } from 'expo-font';
import { useEffect, useState } from 'react';
import { Dimensions, Platform, StatusBar, View } from 'react-native';
import { ControlsBar } from './src/components/ControlsBar';
import { AchievementsScene } from './src/scenes/Achievements';
import { HistoryScene } from './src/scenes/History';
import { PlayScene } from './src/scenes/Play';
import { EventsFactory } from './src/services/EventsFactory';
import { Game } from './src/services/Game';
import { SystemClock } from './src/utils/clock/SystemClock';
import { IAppStorage } from './src/utils/storage/AppStorageInterface';
import { DeviceStorage } from './src/utils/storage/DeviceStorage';
import { DefaultEncrypt } from './src/utils/storage/Encryption/Default';
import { TransparentEncrypt } from './src/utils/storage/Encryption/Transparent';

const DOCUMENT_TITLE = 'button2press';

export default function App() {
    StatusBar.setHidden(true);
    const screen = Dimensions.get('window');
    const [ready, setReady] = useState(false);
    const [showAchievements, setShowAchievements] = useState(false);
    const [showHistory, setShowHistory] = useState(false);
    const [fontsLoaded] = useFonts({
        PrimaryExtraLight: require('./assets/fonts/NotoSans/NotoSans-ExtraLight.ttf'),
        PrimaryLight: require('./assets/fonts/NotoSans/NotoSans-Light.ttf'),
        Primary: require('./assets/fonts/NotoSans/NotoSans-Medium.ttf'),
    });

    useEffect(() => {
        new DeviceStorage(new TransparentEncrypt())
            .get('DEBUG')
            .then((value) => {
                if (value === '1') {
                    Game.enableDebug();
                }
            });

        const clock = new SystemClock();
        let storage: IAppStorage = new DeviceStorage(new TransparentEncrypt());
        if (Platform.OS === 'web') {
            storage = new DeviceStorage(new DefaultEncrypt());
        }

        EventsFactory.init(clock);
        Game.init(storage, clock)
            .then(() => {
                setReady(true);
                if (Game.settings.muted) {
                    Game.sounds.mute();
                } else {
                    Game.sounds.unmute();
                }
            })
            .catch((e) => console.error('Could not initialize game', e));
    }, []);

    return (
        <View
            style={{
                height: screen.height,
                width: screen.width,
                paddingTop: 100,
            }}
        >
            {ready && fontsLoaded && (
                <>
                    <PlayScene />
                    <ControlsBar
                        showAchievements={() => {
                            setShowAchievements(true);
                        }}
                        showHistory={() => {
                            setShowHistory(true);
                        }}
                    />
                    {showAchievements && (
                        <AchievementsScene
                            onClose={() => {
                                setShowAchievements(false);
                            }}
                        />
                    )}
                    {showHistory && (
                        <HistoryScene
                            onClose={() => {
                                setShowHistory(false);
                            }}
                        />
                    )}
                </>
            )}
        </View>
    );
}
