import { IAppClock } from '../../utils/clock/AppClockInterface';
import { GameEvent, SimpleEventsBus } from '../../utils/events/SimpleEventsBus';
import { IAppStorage } from '../../utils/storage/AppStorageInterface';
import { ACHIEVEMENT_EVENTS } from '../events';
import { Achievement } from './Achievements/Achievement';
import { AchievementsCollection } from './Achievements/AchievementsCollection';
import { ButtonPressFailureTracker } from './Achievements/Trackers/ButtonPressFailureTracker';
import { ButtonPressTracker } from './Achievements/Trackers/ButtonPressTracker';
import { ITracker, ITrackerEvent } from './Achievements/Trackers/ITracker';

export class Achievements {
    private readonly STORAGE_KEY_ACHIEVEMENTS = 'UNLOCKED_ACHIEVEMENTS';
    private _collection: AchievementsCollection = new AchievementsCollection();
    private trackers: ITracker[] = [];

    constructor(
        private readonly _clock: IAppClock,
        private readonly _storage: IAppStorage,
        private readonly _events: SimpleEventsBus
    ) {
        this.trackers = [
            new ButtonPressTracker(this._events),
            new ButtonPressFailureTracker(this._events),
        ];
        // Init collection
        this._collection = this._collection.fromJSON(
            JSON.stringify(require('./Achievements/Achievements.json'))
        );
        this._storage.get(this.STORAGE_KEY_ACHIEVEMENTS).then((result) => {
            if (result) {
                const parsed = JSON.parse(result);
                for (const { id, ts, payload } of parsed) {
                    this._collection.unlock(id, ts, payload);
                }
            }
        });
    }

    public getAll(): Achievement[] {
        return this._collection.list;
    }

    public getTotal(): number {
        return 0;
    }

    public triggered(event: GameEvent) {
        if (event.name !== ACHIEVEMENT_EVENTS.TRACKER_TRIGGERED) {
            return;
        }
        const payload: ITrackerEvent = event.payload;
        const unlockedAchievement = this._collection.unlock(
            payload.id,
            event.ts,
            payload.payload
        );
        if (unlockedAchievement) {
            this.persist();
        }
    }

    private persist() {
        this._storage.set(
            this.STORAGE_KEY_ACHIEVEMENTS,
            JSON.stringify(
                this._collection.list
                    .filter((item) => item.unlockTs > 0)
                    .map((item) => ({
                        id: item.id,
                        ts: item.unlockTs,
                    }))
            )
        );
    }

    public unlockAll() {
        this._collection.unlockAll();
        this.persist();
    }

    public lockAll() {
        this._collection = this._collection.fromJSON(
            JSON.stringify(require('./Achievements/Achievements.json'))
        );
        this._storage.delete(this.STORAGE_KEY_ACHIEVEMENTS);
    }
}
