import { IAppClock } from '../utils/clock/AppClockInterface';
import { GameEvent } from '../utils/events/SimpleEventsBus';
import {
    ACHIEVEMENT_EVENTS,
    SETTINGS_EVENTS,
    STAT_EVENTS,
    USER_EVENTS,
} from './events';
import { ITrackerEvent } from './Game/Achievements/Trackers/ITracker';

export class EventsFactory {
    private static _clock: IAppClock;

    public static init(clock: IAppClock) {
        EventsFactory._clock = clock;
    }

    public static userTap(ts: number): GameEvent {
        return {
            name: USER_EVENTS.BUTTON_TAP,
            ts: EventsFactory._clock.now(),
        };
    }

    public static userTapPenalty(ts: number): GameEvent {
        return {
            name: USER_EVENTS.BUTTON_TAP_PENALTY,
            ts: EventsFactory._clock.now(),
        };
    }

    public static totalTapsIncreased(value: number): GameEvent {
        return {
            name: STAT_EVENTS.TOTAL_TAPS,
            ts: EventsFactory._clock.now(),
            payload: { value },
        };
    }

    public static currentButtonTapsIncreased(value: number): GameEvent {
        return {
            name: STAT_EVENTS.CURRENT_BUTTON_TAPS,
            ts: EventsFactory._clock.now(),
            payload: { value },
        };
    }

    public static totalPenaltiesIncreased(value: number): GameEvent {
        return {
            name: STAT_EVENTS.TOTAL_PENALTIES,
            ts: EventsFactory._clock.now(),
            payload: { value },
        };
    }

    public static currentButtonPenaltiesIncreased(value: number): GameEvent {
        return {
            name: STAT_EVENTS.CURRENT_BUTTON_PENALTIES,
            ts: EventsFactory._clock.now(),
            payload: { value },
        };
    }

    public static achievementsTrackerTriggered(
        payload: ITrackerEvent
    ): GameEvent {
        return {
            ts: EventsFactory._clock.now(),
            name: ACHIEVEMENT_EVENTS.TRACKER_TRIGGERED,
            payload: payload,
        };
    }

    public static achievementUnlocked(payload: any): GameEvent {
        return {
            ts: EventsFactory._clock.now(),
            name: ACHIEVEMENT_EVENTS.UNLOCKED,
            payload: payload,
        };
    }

    public static buttonExpired(taps: number): GameEvent {
        return {
            name: USER_EVENTS.BUTTON_EXPIRED,
            ts: EventsFactory._clock.now(),
            payload: { taps },
        };
    }

    public static toggleMute(muted: boolean): GameEvent {
        return {
            name: SETTINGS_EVENTS.TOGGLE_MUTE,
            ts: EventsFactory._clock.now(),
            payload: { muted },
        };
    }
}
