export interface GameEvent {
    ts: number;
    name: string;
    payload?: any;
}

export class SimpleEventsBus {
    private static __instance: SimpleEventsBus;
    private subscriptions: any;

    constructor() {
        this.subscriptions = {};
    }

    public static instance(): SimpleEventsBus {
        if (SimpleEventsBus.__instance) {
            return SimpleEventsBus.__instance;
        }

        SimpleEventsBus.__instance = new SimpleEventsBus();
        return SimpleEventsBus.__instance;
    }

    public subscribe(event: string, callback: CallableFunction): string {
        // Random enough ID generation :D
        const id = `${Date.now()}-${Math.random()}`;
        if (!this.subscriptions[event]) {
            this.subscriptions[event] = {};
        }

        this.subscriptions[event][id] = callback;
        return id;
    }

    public unsubscribe(id: string) {
        for (const event in this.subscriptions) {
            if (this.subscriptions[event][id]) {
                delete this.subscriptions[event][id];
            }
        }
    }

    public emit(event: GameEvent) {
        const key = event.name;
        if (!this.subscriptions[key]) {
            return;
        }
        for (const id in this.subscriptions[key]) {
            this.subscriptions[key][id](event);
        }
    }

    public subs() {
        return this.subscriptions;
    }
}
