import { FC, useState } from 'react';
import { Pressable } from 'react-native';
import { MutedSvg } from '../../Icons/Common/MutedSvg';
import { UnmutedSvg } from '../../Icons/Common/UnmutedSvg';
import { EventsFactory } from '../../services/EventsFactory';
import { Game } from '../../services/Game';

export const ToggleSoundButton: FC = () => {
    const size = 30;
    const [muted, setMuted] = useState(Game.settings.muted);
    return (
        <Pressable
            onPress={() => {
                if (muted) {
                    Game.sounds.unmute();
                } else {
                    Game.sounds.mute();
                }
                Game.events.emit(EventsFactory.toggleMute(!muted));
                setMuted(!muted);
            }}
        >
            {muted && <MutedSvg width={size} height={size} fill="#B4C0D0" />}
            {!muted && <UnmutedSvg width={size} height={size} fill="#778da9" />}
        </Pressable>
    );
};
