import { FC } from 'react';
import { Pressable, ScrollView, Text, View } from 'react-native';
import { GreenButton } from '../basic-components/Buttons/GreenButton';
import { Game } from '../services/Game';
import { AchievementItem } from './Achievements/AchievementItem';
import { AchievementsHeader } from './Achievements/Header';

interface IProps {
    onClose: CallableFunction;
}

export const HistoryScene: FC<IProps> = ({ onClose }) => {
    return (
        <View
            style={{
                position: 'absolute',
                top: 0,
                bottom: 0,
                left: 0,
                right: 0,
                backgroundColor: '#fff',
                padding: 20,
            }}
        >
            <View>
                <Text>Buttons you've played</Text>
                <Pressable
                    onPress={() => {
                        onClose();
                    }}
                >
                    <Text>Close</Text>
                </Pressable>
            </View>
            <ScrollView
                style={{ marginTop: 70 }}
                showsVerticalScrollIndicator={false}
            >
                {Game.history.buttons
                    .sort((a, b) => {
                        if (a.taps < b.taps) {
                            return 1;
                        }
                        if (a.taps > b.taps) {
                            return -1;
                        }
                        return 0;
                    })
                    .map((item) => (
                        <View key={`${item.taps}:${item.expiredAt}`}>
                            <GreenButton
                                shadowHeight={15}
                                text={`${item.taps}`}
                                onPress={() => {}}
                            />
                            <Text>Expired on: {item.expiredAt}</Text>
                        </View>
                    ))}
            </ScrollView>
        </View>
    );
};
