import { FC, useRef, useState } from 'react';
import { Animated, Text, View } from 'react-native';
import { GreenButton } from '../basic-components/Buttons/GreenButton';
import { RedButton } from '../basic-components/Buttons/RedButton';
import { useInterval } from '../hooks/useInterval';
import { EventsFactory } from '../services/EventsFactory';
import { Game } from '../services/Game';
import { Sounds } from '../services/Sounds';
import { CountDown } from './Play/CountDown';
import { DebugPanel } from './Play/DebugPanel';
import { GameScore } from './Play/Score';

export const PlayScene: FC = () => {
    const buttonController = Game.controller;
    const [counter, setCounter] = useState(buttonController.getCounter());
    const [canRenderPenaltyAnimation, setCanRenderPenaltyAnimation] =
        useState(true);
    const [disabled, setDisabled] = useState(
        Game.clock.now() <= buttonController.getNextPressTimeFrameStart()
    );

    const penaltyTimeOpacity = useRef(new Animated.Value(0)).current;
    const penaltyTimeAnimation = Animated.sequence([
        Animated.timing(penaltyTimeOpacity, {
            useNativeDriver: false,
            toValue: 1,
            duration: 10,
        }),
        Animated.timing(penaltyTimeOpacity, {
            useNativeDriver: false,
            toValue: 0,
            duration: 500,
        }),
    ]);

    const pressHandle = () => {
        buttonController.buttonPressedOnTime();
        setCounter(buttonController.getCounter());
        setDisabled(true);
        Game.events.emit(EventsFactory.userTap(Game.clock.now()));
    };

    const penaltyHandler = () => {
        Game.events.emit(EventsFactory.userTapPenalty(Game.clock.now()));
        buttonController.buttonPressedTooEarly();
        if (canRenderPenaltyAnimation) {
            setCanRenderPenaltyAnimation(false);
            penaltyTimeAnimation.start(() => {
                setCanRenderPenaltyAnimation(true);
            });
        }
    };

    const reset = () => {
        buttonController.resetButton();
        setCounter(buttonController.getCounter());
        setDisabled(false);
    };

    useInterval(() => {
        if (buttonController.isButtonExpired()) {
            Game.events.emit(
                // Send current button taps count with an event
                EventsFactory.buttonExpired(buttonController.getCounter())
            );
            buttonController.resetButton();
            setCounter(buttonController.getCounter());
        }
        setDisabled(
            Game.clock.now() <= buttonController.getNextPressTimeFrameStart()
        );
    }, 400);

    return (
        <View>
            <View style={{ alignItems: 'center' }}>
                <GameScore score={counter} />

                {disabled && (
                    <RedButton
                        text={`DO NOT PRESS`}
                        onPress={penaltyHandler}
                        shadowHeight={15}
                        disabled={disabled}
                        sound={Sounds.SOUND_BUTTON_PENALTY}
                    />
                )}
                {!disabled && (
                    <GreenButton
                        text={`PRESS ME`}
                        onPress={pressHandle}
                        shadowHeight={15}
                        disabled={disabled}
                        sound={Sounds.SOUND_BUTTON_PRESS}
                    />
                )}
                {disabled && (
                    <View style={{ paddingTop: 20 }}>
                        <CountDown
                            color="#DE0261"
                            endTime={buttonController.getNextPressTimeFrameStart()}
                        />
                        <Animated.View
                            style={{
                                position: 'absolute',
                                right: -40,
                                top: 20,
                                opacity: penaltyTimeOpacity,
                            }}
                        >
                            <Text
                                style={{ color: '#FF006E', fontWeight: 'bold' }}
                            >
                                +
                                {Math.floor(
                                    Game.controller.getPenaltyTime() / 1000
                                )}
                            </Text>
                        </Animated.View>
                    </View>
                )}

                {!disabled && (
                    <View style={{ paddingTop: 20 }}>
                        <CountDown
                            color="#02B788"
                            endTime={buttonController.getNextPressTimeFrameEnd()}
                        />
                    </View>
                )}

                {Game.debug && <DebugPanel reset={reset} />}
            </View>
        </View>
    );
};
