import { IAppClock } from '../../utils/clock/AppClockInterface';
import { IAppStorage } from '../../utils/storage/AppStorageInterface';
import { Game } from '../Game';
import { TimingManager } from './TimingManager';
import { UserStats } from './UserStats';

export class ButtonController {
    private _taps: number = 0;
    private _nextTapTs: number = -1;
    private timingManager: TimingManager;
    private CONFIG_STORAGE_KEY = 'GAME_CONFIG';

    constructor(
        private readonly _clock: IAppClock,
        private readonly _storage: IAppStorage
    ) {
        this.timingManager = new TimingManager();
    }

    public async init(taps: number) {
        this._taps = taps;
        const config = await this._storage.get(this.CONFIG_STORAGE_KEY);
        if (config) {
            const data = JSON.parse(config);
            this._nextTapTs = data.nextTapTs ?? -1;
            return;
        }
        this.resetButton();
    }

    public async persistConfig() {
        await this._storage.set(
            this.CONFIG_STORAGE_KEY,
            JSON.stringify({
                nextTapTs: this._nextTapTs,
            })
        );
    }

    public getNextPressTimeFrameStart(): number {
        return this._nextTapTs;
    }

    public getNextPressTimeFrameEnd(): number {
        return this._nextTapTs + this.timingManager.getPressTimeFrame();
    }

    public buttonPressedOnTime() {
        this._taps++;
        this._nextTapTs =
            this._clock.now() +
            this.timingManager.getCoolDown(this.getCounter());
        this.persistConfig();
    }

    public isButtonExpired(): boolean {
        if (this._nextTapTs == -1) {
            return false;
        }
        return this._clock.now() > this.getNextPressTimeFrameEnd();
    }

    public resetButton() {
        this._nextTapTs = -1;
        this._taps = 0;
        this.persistConfig();
    }

    public getPenaltyTime(): number {
        return this.timingManager.getPenaltyTime(this.getCounter());
    }

    public buttonPressedTooEarly() {
        this._nextTapTs = this._nextTapTs + this.getPenaltyTime();
        this.persistConfig();
    }

    public getCounter(): number {
        return this._taps;
    }

    public getPressedTimesNumber(): number {
        return 0;
    }

    public canBePressed(): boolean {
        return true;
    }

    public expired(): boolean {
        return false;
    }
}
