import { FC } from 'react';
import { BlockSvg } from '../../../Icons/AchievementIcon/BlockSvg';
import { ClickFilledSvg } from '../../../Icons/AchievementIcon/ClickFilledSvg';
import { ClickSvg } from '../../../Icons/AchievementIcon/ClickSvg';
import { FingersBrokenSvg } from '../../../Icons/AchievementIcon/FingersBrokenSvg';
import { HighFiveSvg } from '../../../Icons/AchievementIcon/HighFiveSvg';
import { LearningSvg } from '../../../Icons/AchievementIcon/LearningSvg';
import { LockedSvg } from '../../../Icons/AchievementIcon/LockedSvg';
import { PressSvg } from '../../../Icons/AchievementIcon/PressSvg';
import { StrongSvg } from '../../../Icons/AchievementIcon/StrongSvg';
import { SuperheroSvg } from '../../../Icons/AchievementIcon/SuperheroSvg';
import { WaitingSvg } from '../../../Icons/AchievementIcon/WaitingSvg';
import { WrongTapSvg } from '../../../Icons/AchievementIcon/WrongTapSvg';
import { Achievement } from '../../../services/Game/Achievements/Achievement';

interface IProps {
    achievement: Achievement;
}

const ACHIEVEMENTS_COLORS: { [key: string]: string } = {
    'PRESS:1': '#515151',
    'PRESS:5': '#515151',
    'PRESS:20': '#02B788',
    'PRESS:100': '#02B788',
    'PRESS:500': '#515151',

    'PENALTY:1': '#FF99C8',
    'PENALTY:5': '#E71D36',
    'PENALTY:20': '#E71D36',
    'PENALTY:100': '#E71D36',
    'PENALTY:500': '#E71D36',
};

const getColorById = (achievementId: string) => {
    return ACHIEVEMENTS_COLORS[achievementId] ?? '#000';
};

export const AchievementIcon: FC<IProps> = ({ achievement }) => {
    const width = 35;
    const height = 35;
    const isLocked = achievement.unlockTs < 0;
    const fill = getColorById(achievement.id);
    if (isLocked) {
        return <LockedSvg width={width} height={height} />;
    }

    switch (achievement.id) {
        case 'PRESS:1':
            return <PressSvg width={width} height={height} fill={fill} />;
        case 'PRESS:5':
            return <HighFiveSvg width={width} height={height} fill={fill} />;
        case 'PRESS:20':
            return <ClickSvg width={width} height={height} fill={fill} />;
        case 'PRESS:100':
            return <StrongSvg width={width} height={height} fill={fill} />;
        case 'PRESS:500':
            return (
                <FingersBrokenSvg width={width} height={height} fill={fill} />
            );

        case 'PENALTY:1':
            return <WrongTapSvg width={width} height={height} fill={fill} />;
        case 'PENALTY:5':
            return <WaitingSvg width={width} height={height} fill={fill} />;
        case 'PENALTY:20':
            return <LearningSvg width={width} height={height} fill={fill} />;
        case 'PENALTY:100':
            return <SuperheroSvg width={width} height={height} fill={fill} />;
    }

    return <BlockSvg width={width} height={height} fill={fill} />;
};
