import { IAppClock } from '../../utils/clock/AppClockInterface';
import { SimpleEventsBus } from '../../utils/events/SimpleEventsBus';
import { IAppStorage } from '../../utils/storage/AppStorageInterface';
import { EventsFactory } from '../EventsFactory';

export class UserStats {
    private STORAGE_KEY = 'USER_STATS';
    private totalTaps: number = 0;
    private totalPenalties: number = 0;
    private _currentTaps: number = 0;

    constructor(
        private readonly clock: IAppClock,
        private readonly storage: IAppStorage,
        private readonly events: SimpleEventsBus
    ) {}

    public get currentTaps(): number {
        return this._currentTaps;
    }

    public tap(event: any) {
        this.totalTaps++;
        this._currentTaps++;
        this.events.emit(EventsFactory.totalTapsIncreased(this.totalTaps));
        this.persist();
    }

    public tapPenalty(event: any) {
        this.totalPenalties++;
        this.events.emit(
            EventsFactory.totalPenaltiesIncreased(this.totalPenalties)
        );
        this.persist();
    }

    public expireButton() {
        this._currentTaps = 0;
        this.persist();
    }

    public persist() {
        this.storage.set(
            this.STORAGE_KEY,
            JSON.stringify({
                totals: {
                    taps: this.totalTaps,
                    penalties: this.totalPenalties,
                },
                current: {
                    taps: this._currentTaps,
                },
            })
        );
    }

    public async init() {
        const json = await this.storage.get(this.STORAGE_KEY);
        if (json) {
            const data: any = JSON.parse(json);

            this.totalTaps = data?.totals?.taps ?? 0;
            this.totalPenalties = data?.totals?.penalties ?? 0;
            this._currentTaps = data?.current?.taps ?? 0;
        }
    }
}
