import { FC } from 'react';
import { Pressable, Text, View } from 'react-native';
import { Game } from '../../services/Game';
import { LockSvg } from './DebugPanel/LockSvg';
import { ResetSvg } from './DebugPanel/ResetSvg';
import { UnlockSvg } from './DebugPanel/UnlockSvg';

interface IProps {
    reset: CallableFunction;
}

export const DebugPanel: FC<IProps> = ({ reset }) => {
    return (
        <View style={{ marginTop: 20, flexDirection: 'row' }}>
            <View style={{ marginRight: 50 }}>
                <Pressable
                    onPress={() => {
                        reset();
                    }}
                >
                    <ResetSvg width={50} height={50} />
                </Pressable>
            </View>
            <View style={{ marginRight: 50 }}>
                <Pressable
                    onPress={() => {
                        Game.achievements.unlockAll();
                    }}
                >
                    <UnlockSvg width={50} height={50} />
                </Pressable>
            </View>

            <View>
                <Pressable
                    onPress={() => {
                        Game.achievements.lockAll();
                    }}
                >
                    <LockSvg width={50} height={50} />
                </Pressable>
            </View>
        </View>
    );
};
